.logo-wrapper {
  display: flex;
  align-items: flex-start;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  padding: 5px 0;
  color: #fff;
  text-align: center;
  fill: $beige;

  @media(max-width:1023px) {
    padding: 0;
    &__brand {
      margin-bottom: 0;
    }
    &__name {
      display: none;
    }
    &__desc {
      display: none;
    }
    &__footer {
      display: none;
    }
  }

  @media(max-width:767px) {
    &__brand {
      width: 142px;
      height: 57px;
    }
  }

  // .logo__brand
  &__brand {
    margin-bottom: 10px;
  }

  // .logo__name
  &__name {
    margin-bottom: 6px;
  }

  // .logo__desc
  &__desc {
    display: inline-block;
    width: 270px;
    margin: 0 -5px 7px;
    font-size: 10px;
    font-family: CormorantGaramond, Courier, monospace;
    line-height: 1.4;
    letter-spacing: 0.21em;
    text-transform: uppercase;
    user-select: none;
  }
}
